@use "sass:map";
/* 
    IMAGE BORDERS 
*/
nav {
  flex-direction: column;
  display: flex; }
  @media screen and (min-width: 50rem) {
    nav {
      display: none; } }
  @media screen and (min-width: 75rem) {
    nav {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-end;
      padding: 30px 0; } }
