@use "sass:map";
/* 
    IMAGE BORDERS 
*/
.Header {
  position: fixed;
  z-index: 100;
  right: 0;
  bottom: 0;
  left: 0;
  height: 54px;
  pointer-events: none; }
  .Header-bottomWrap {
    position: absolute;
    display: grid;
    align-items: center;
    grid-auto-flow: column;
    grid-template-areas: "logo social nav";
    grid-template-columns: 1fr min-content min-content;
    background-color: var(--brush-white);
    bottom: 0;
    overflow-x: visible;
    overflow-y: hidden;
    width: 100vw;
    height: 54px;
    pointer-events: initial; }
    @media screen and (min-width: 50rem) {
      .Header-bottomWrap {
        grid-template-areas: "nav social";
        grid-template-columns: 1fr min-content; }
        .Header-bottomWrap .Logo {
          display: none; } }
    @media screen and (min-width: 75rem) {
      .Header-bottomWrap {
        background-color: transparent;
        width: 350px; }
        .Header-bottomWrap-mobileControls {
          display: none; } }
    .Header-bottomWrap::-webkit-scrollbar {
      display: none; }
  @media screen and (min-width: 75rem) {
    .Header {
      display: none; } }
