@use "sass:map";
/* 
    IMAGE BORDERS 
*/
.Footer {
  display: grid;
  grid-template-columns: 1fr auto;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  min-height: 100px;
  height: 20vw;
  max-height: 150px; }
  .Footer-credits {
    box-sizing: border-box;
    display: grid;
    font-family: "Work Sans", sans-serif;
    background: var(--brush-black);
    justify-items: end;
    align-items: end;
    bottom: 0;
    right: 0;
    padding: 15px;
    font-size: 1rem;
    width: 20vw;
    min-width: 100px;
    max-width: 150px;
    height: 100%; }
  .Footer-art {
    display: block;
    width: 100%;
    background: url("/wp-content/uploads/footer-art-bg.svg") no-repeat bottom right; }
    .Footer-art-fg {
      max-height: 200px;
      min-height: 50px;
      height: calc(100vw/4);
      position: absolute;
      width: 80vw;
      max-width: calc(100vw - 100px);
      min-width: calc(100vw - 150px);
      bottom: 20px; }
  .Footer-yearAndName {
    color: var(--brush-white);
    text-align: right; }
  .Footer a {
    text-decoration: none; }
