@use "sass:map";
/* 
    IMAGE BORDERS 
*/
.Spinner {
  position: fixed;
  padding: 3rem;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: none;
  justify-items: center;
  grid-row-gap: 1rem;
  grid-template-rows: 45% 65%;
  z-index: 110;
  background: radial-gradient(closest-side, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.6));
  grid-template-areas: "logo" "LoadMessage"; }
  .Spinner-logo {
    grid-area: logo;
    align-self: end;
    filter: var(--filter-darkGray);
    max-height: 60px; }
  .Spinner-loadMessage {
    grid-area: LoadMessage;
    font-family: "Work Sans", sans-serif;
    align-self: top;
    color: var(--brush-darkGray); }
    @media screen and (min-width: 50rem) {
      .Spinner-loadMessage {
        font-size: 25px; } }
