@use "sass:map";
/* 
    IMAGE BORDERS 
*/
.Canvas {
  min-height: calc(100vh - 54px + 100px);
  padding-bottom: 200px; }
  @media screen and (min-width: 50rem) {
    .Canvas {
      display: grid;
      grid-template-areas: ". Heading-h2" ". Article" ". PostList-wrapper";
      grid-template-rows: 150px 1fr;
      grid-template-columns: 25vw 2fr;
      grid-column-gap: calc( 30px + (100 - 30) * ((100vw - 800px) / (1920 - 800))); } }
  @media screen and (min-width: 75rem) {
    .Canvas {
      grid-template-areas: ". Heading-h2 ." ". Article ." ". PostList-wrapper .";
      grid-template-columns: 25vw 2fr 180px; } }
  .Canvas-decor {
    grid-area: Canvas-decor;
    grid-template-areas: "Heading-h2";
    display: grid;
    position: relative;
    min-height: 200px;
    width: 100%; }
    @media screen and (min-width: 50rem) {
      .Canvas-decor {
        position: fixed;
        left: 0;
        bottom: 0;
        top: 0;
        width: 25vw; } }
    .Canvas-decor > .Canvas-featureTitle {
      align-self: end;
      display: block;
      bottom: 0;
      box-sizing: border-box;
      width: 100%;
      padding: var(--accent-thickness);
      text-align: center;
      overflow: hidden; }
      @media screen and (min-width: 50rem) {
        .Canvas-decor > .Canvas-featureTitle {
          display: none; } }
  .Canvas .Canvas-articleTitle {
    display: none;
    align-self: center; }
    @media screen and (min-width: 50rem) {
      .Canvas .Canvas-articleTitle {
        display: block; } }
  .Canvas article {
    grid-area: Article;
    padding-top: var(--accent-thickness);
    padding-right: var(--accent-thickness);
    padding-bottom: 200px;
    padding-left: var(--accent-thickness); }
    @media screen and (min-width: 50rem) {
      .Canvas article {
        padding-top: 0;
        padding-left: 0; } }
    .Canvas article p:first-child {
      margin-top: 0; }
