@use "sass:map";
/* 
    IMAGE BORDERS 
*/
.PostList-card {
  display: grid;
  grid-template-areas: "image" "Heading-h4" "date" "excerpt";
  grid-row-gap: 15px;
  text-decoration: none;
  padding: 30px; }
  @media screen and (min-width: 50rem) {
    .PostList-card {
      grid-template-areas: "image Heading-h4" "image date" "image excerpt";
      grid-template-rows: min-content min-content auto;
      grid-row-gap: 15px;
      grid-column-gap: 30px; } }
  .PostList-card-date {
    grid-area: date;
    color: var(--brush-gray); }
  .PostList-card-excerpt {
    grid-area: excerpt; }
    .PostList-card-excerpt > p {
      margin: 0; }
  .PostList-card-thumbnail {
    grid-area: image;
    overflow: hidden; }
    .PostList-card-thumbnail > img {
      width: calc(100vw - (2 * var(--accent-thickness)));
      height: auto; }
      @media screen and (min-width: 50rem) {
        .PostList-card-thumbnail > img {
          width: 250px;
          height: auto; } }
