@use "sass:map";
/* 
    IMAGE BORDERS 
*/
@font-face {
  font-family: 'KendiniYenidenKesfet';
  src: url(/wp-content/themes/basakbeykoz/static/media/KendiniYenidenKesfet.cf469d17.woff2) format("woff2"), url(/wp-content/themes/basakbeykoz/static/media/KendiniYenidenKesfet.c846d943.woff) format("woff"), url(/wp-content/themes/basakbeykoz/static/media/KendiniYenidenKesfet.85b78429.ttf) format("truetype"); }

:root {
  /* measurements */
  --wp-frame-width: 1200px;
  --wp-frame-padding-x: 7em;
  --wp-text-with: 800px;
  --accent-thickness: 30px;
  --home-vertical-padding: 200px;
  --home-horizontal-padding: 400px;
  --fixed-feature-image-width: 470px;
  --head-section-height: 110px; }

/* 
    SCROLLBAR 
*/
::-webkit-scrollbar {
  width: 0; }

::-webkit-scrollbar-track {
  background: linear-gradient(180deg, var(--brush-gray) 30px, var(--brush-darkGray) 30px, var(--brush-darkGray) 130px, var(--brush-gray) 130px, var(--brush-gray) calc(50vh - 156px), var(--brush-darkYellow) calc(50vh - 156px), var(--brush-darkYellow) calc(50vh + 156px), var(--brush-gray) calc(50vh - 156px)); }

::-webkit-scrollbar-thumb {
  background: var(--brush-black); }

@media screen and (min-width: 50rem) {
  ::-webkit-scrollbar {
    width: 15px; } }

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--brush-white);
  color: var(--brush-darkBlue);
  overflow-y: scroll;
  overflow-x: hidden;
  font-size: 20px; }

h1 {
  grid-area: Heading-h1;
  color: var(--brush-darkBlue);
  margin: 0;
  font-size: calc( 50px + (110 - 50) * ((100vw - 320px) / (1920 - 320)));
  line-height: calc( 70px + (140 - 70) * ((100vw - 320px) / (1920 - 320)));
  font-family: KendiniYenidenKesfet, sans-serif;
  font-weight: 100; }

h2 {
  grid-area: Heading-h2;
  color: var(--brush-darkBlue);
  margin: 0;
  font-size: calc( 26px + (40 - 26) * ((100vw - 320px) / (1920 - 320)));
  line-height: calc( 30px + (45 - 30) * ((100vw - 320px) / (1920 - 320)));
  font-family: Montserrat, sans-serif; }

h3 {
  grid-area: Heading-h3;
  color: var(--brush-darkBlue);
  margin: 0;
  font-size: calc( 23px + (35 - 23) * ((100vw - 320px) / (1920 - 320)));
  line-height: calc( 28px + (40 - 28) * ((100vw - 320px) / (1920 - 320)));
  font-family: Montserrat, sans-serif; }

h4 {
  grid-area: Heading-h4;
  color: var(--brush-darkBlue);
  margin: 0;
  font-size: calc( 20px + (25 - 20) * ((100vw - 320px) / (1920 - 320)));
  line-height: calc( 25px + (30 - 25) * ((100vw - 320px) / (1920 - 320)));
  font-family: Montserrat, sans-serif; }

h5 {
  grid-area: Heading-h5;
  color: var(--brush-darkBlue);
  margin: 0;
  font-size: calc( 16px + (26 - 16) * ((100vw - 320px) / (1920 - 320)));
  line-height: calc( 20px + (34 - 20) * ((100vw - 320px) / (1920 - 320)));
  font-family: Montserrat, sans-serif; }

h6 {
  grid-area: Heading-h6;
  color: var(--brush-darkBlue);
  margin: 0;
  font-size: calc( 14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));
  line-height: calc( 18px + (24 - 18) * ((100vw - 320px) / (1920 - 320)));
  font-family: Montserrat, sans-serif; }

.body.page.home > article > div .wp-block-latest-posts > li,
span, :not(h1 > span)
nav {
  color: var(--brush-lightBlue);
  font-family: "Work Sans", sans-serif; }

.wp-block-button__link,
figcaption {
  font-family: "Montserrat", sans-serif;
  margin: 0; }

p {
  grid-area: paragraph;
  color: var(--brush-darkBlue);
  font-family: "Work Sans", sans-serif;
  font-size: calc( 20px + (26 - 20) * ((100vw - 320px) / (1920 - 320)));
  line-height: calc( 26px + (38 - 26) * ((100vw - 320px) / (1920 - 320)));
  margin: 0 0 30px 0; }

/*
    COLORS
*/
:root {
  --brush-white: #FBFBFB;
  --brush-lightGray: #F8F8F8;
  --brush-gray: #859FA4;
  --brush-darkGray: #34414B;
  --brush-black: #34414B;
  --brush-lightBlue: #ADCBDD;
  --brush-darkBlue: #324358;
  --brush-lightYellow: #FFEEC6;
  --brush-darkYellow: #FFC05B;
  --brush-lightGreen: #C6DFD7;
  --filter-white: contrast(1) invert(96%) sepia(4%) saturate(282%) hue-rotate(202deg) brightness(102%) contrast(95%);
  --filter-darkGray: brightness(0) invert(20%) sepia(6%) saturate(2240%) hue-rotate(164deg) brightness(98%) contrast(84%);
  --filter-logo-white: brightness(0) contrast(1) invert(96%) sepia(4%) saturate(282%) hue-rotate(202deg) brightness(102%) contrast(95%); }

.text-light {
  color: var(--brush-white); }

.text-mediumLight {
  color: var(--brush-darkBlue); }

.text-mediumDark {
  color: var(--brush-darkBlue); }

.text-dark {
  color: var(--brush-black); }

.bg-green {
  background-color: var(--brush-lightGreen); }

.green-horizontal-border:before {
  background: linear-gradient(90deg, var(--brush-lightGreen) calc(30px/2), transparent calc(30px/2), transparent calc(100% - 30px/2), var(--brush-lightGreen) calc(100% - 30px/2)); }

.green-vertical-border:after {
  background: linear-gradient(0deg, var(--brush-lightGreen) calc(30px/2), transparent calc(30px/2), transparent calc(100% - 30px/2), var(--brush-lightGreen) calc(100% - 30px/2)); }

.bg-yellow {
  background-color: var(--brush-lightYellow); }

.yellow-horizontal-border:before {
  background: linear-gradient(90deg, var(--brush-lightYellow) calc(30px/2), transparent calc(30px/2), transparent calc(100% - 30px/2), var(--brush-lightYellow) calc(100% - 30px/2)); }

.yellow-vertical-border:after {
  background: linear-gradient(0deg, var(--brush-lightYellow) calc(30px/2), transparent calc(30px/2), transparent calc(100% - 30px/2), var(--brush-lightYellow) calc(100% - 30px/2)); }

.bg-white {
  background-color: var(--brush-white); }

.white-horizontal-border:before {
  background: linear-gradient(90deg, var(--brush-white) calc(30px/2), transparent calc(30px/2), transparent calc(100% - 30px/2), var(--brush-white) calc(100% - 30px/2)); }

.white-vertical-border:after {
  background: linear-gradient(0deg, var(--brush-white) calc(30px/2), transparent calc(30px/2), transparent calc(100% - 30px/2), var(--brush-white) calc(100% - 30px/2)); }

.bg-blue {
  background-color: var(--brush-lightBlue); }

.blue-horizontal-border:before {
  background: linear-gradient(90deg, var(--brush-lightBlue) calc(30px/2), transparent calc(30px/2), transparent calc(100% - 30px/2), var(--brush-lightBlue) calc(100% - 30px/2)); }

.blue-vertical-border:after {
  background: linear-gradient(0deg, var(--brush-lightBlue) calc(30px/2), transparent calc(30px/2), transparent calc(100% - 30px/2), var(--brush-lightBlue) calc(100% - 30px/2)); }

.bg-black {
  background-color: var(--brush-black); }

.black-horizontal-border:before {
  background: linear-gradient(90deg, var(--brush-black) calc(30px/2), transparent calc(30px/2), transparent calc(100% - 30px/2), var(--brush-black) calc(100% - 30px/2)); }

.black-vertical-border:after {
  background: linear-gradient(0deg, var(--brush-black) calc(30px/2), transparent calc(30px/2), transparent calc(100% - 30px/2), var(--brush-black) calc(100% - 30px/2)); }
