@use "sass:map";
/* 
    IMAGE BORDERS 
*/
.NavContainer {
  display: none;
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }
  @media screen and (min-width: 50rem) {
    .NavContainer {
      display: none; } }
  @media screen and (min-width: 75rem) {
    .NavContainer {
      display: block;
      right: 0;
      width: auto;
      left: auto;
      top: 50vh;
      height: min-content;
      transform: translateY(-50%);
      z-index: 100; } }
  .NavContainer.open {
    display: block; }
  .NavContainer-list {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: var(--brush-white); }
    @media screen and (min-width: 75rem) {
      .NavContainer-list {
        position: relative;
        margin-right: 15px;
        background-color: transparent; } }
  .NavContainer-decor {
    display: none; }
    @media screen and (min-width: 75rem) {
      .NavContainer-decor {
        display: block;
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        width: 15px;
        background-color: var(--brush-darkYellow); } }
  .NavContainer-mask {
    position: fixed;
    background: linear-gradient(rgba(255, 255, 255, 0.6), transparent calc(100vh - 54px));
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; }
    @media screen and (min-width: 50rem) {
      .NavContainer-mask {
        display: none; } }
