@use "sass:map";
/* 
    IMAGE BORDERS 
*/
.BurgerMenu {
  grid-area: nav;
  display: flex; }
  .BurgerMenu-burgerButton {
    background-color: var(--brush-white);
    color: var(--brush-black);
    border: none;
    padding: 0;
    margin: 0;
    height: 50px;
    cursor: pointer; }
    .BurgerMenu-burgerButton:focus {
      border: none;
      outline: none; }
    .BurgerMenu-burgerButton > img {
      min-height: 20px;
      min-width: 20px;
      height: calc( 20px + (30 - 20) * ((100vw - 1200px) / (1920 - 1200)));
      width: calc( 20px + (30 - 20) * ((100vw - 1200px) / (1920 - 1200)));
      padding: 15px;
      filter: var(--filter-white);
      filter: var(--filter-darkGray); }
  @media screen and (min-width: 50rem) {
    .BurgerMenu {
      display: none; } }
