@use "sass:map";
/* 
    IMAGE BORDERS 
*/
.PostList {
  grid-area: PostList; }
  .PostList-wrapper {
    grid-area: PostList-wrapper; }
    .PostList-wrapper > h4 {
      text-align: center; }
      @media screen and (min-width: 50rem) {
        .PostList-wrapper > h4 {
          text-align: left; } }
  .PostList li {
    list-style: none;
    margin-bottom: 60px; }
    article .PostList li {
      padding: 0; }
  .PostList ol {
    margin: 0;
    padding: 0; }
  @media screen and (min-width: 50rem) {
    .PostList {
      margin-left: -30px; } }
