@use "sass:map";
/* 
    IMAGE BORDERS 
*/
.Social-icon {
  min-height: 20px;
  min-width: 20px;
  height: calc( 20px + (30 - 20) * ((100vw - 1200px) / (1920 - 1200)));
  width: calc( 20px + (30 - 20) * ((100vw - 1200px) / (1920 - 1200)));
  padding: 15px;
  filter: var(--filter-white);
  filter: var(--filter-darkGray); }

.Social a {
  min-height: 50px;
  height: calc(30px + calc( 20px + (30 - 20) * ((100vw - 1200px) / (1920 - 1200)))); }
