@use "sass:map";
/* 
    IMAGE BORDERS 
*/
.Nav-item {
  display: block;
  padding: 15px 30px;
  position: relative;
  font-family: "Work Sans", sans-serif;
  text-decoration: none;
  color: var(--brush-black);
  text-align: right;
  white-space: nowrap; }
  @media screen and (min-width: 75rem) {
    .Nav-item {
      color: var(--brush-darkGray);
      padding: 7.5px 15px;
      font-size: calc( 20px + (26 - 20) * ((100vw - 320px) / (1920 - 320))); } }
  .Nav-item-active {
    color: var(--brush-darkYellow); }
    .Nav-item-active:after {
      position: absolute;
      content: "";
      top: 0;
      bottom: 0;
      right: 0;
      width: 15px;
      background-color: var(--brush-darkYellow); }
      @media screen and (min-width: 50rem) {
        .Nav-item-active:after {
          width: auto;
          height: 7.5px;
          top: auto;
          right: 15px;
          left: 15px; } }
      @media screen and (min-width: 75rem) {
        .Nav-item-active:after {
          left: auto;
          bottom: 0;
          top: 0;
          right: 0;
          width: 7.5px;
          height: auto; } }
