@use "sass:map";
/* 
    IMAGE BORDERS 
*/
.Logo {
  grid-area: logo; }
  .Logo-decor {
    width: var(--accent-thickness);
    height: 100px;
    background: var(--brush-darkGray);
    left: 0; }
  .Logo-logo > img {
    max-width: 140px;
    min-width: 70px; }
  header .Logo .Logo-logo > img {
    margin-left: 15px;
    margin-top: 8px; }
  header .Logo .Logo-decor {
    display: none; }
  .App-wideScreenControls .Logo {
    display: none;
    z-index: 90;
    position: fixed;
    top: 30px;
    left: 0;
    flex-direction: row;
    align-items: center; }
    @media screen and (min-width: 50rem) {
      .App-wideScreenControls .Logo {
        display: flex; }
        .App-wideScreenControls .Logo img {
          max-width: 350px;
          min-width: 170px;
          width: calc(25vw - 15px - 60px);
          margin-top: calc(calc(25vw - 15px - 60px) / 25);
          padding-left: 15px; } }
