@use "sass:map";
/* 
    IMAGE BORDERS 
*/
.Social {
  align-items: center;
  grid-column-gap: calc( var(--accent-thickness) / 2); }
  .Social-decor {
    width: 15px;
    height: 100px;
    background: var(--brush-darkGray);
    right: 0; }
  .Social-list {
    display: flex;
    overflow: auto;
    flex-direction: row;
    justify-items: right;
    padding-left: 0;
    overflow: hidden; }
  .Header .Social {
    grid-area: social;
    display: flex;
    height: 100%; }
    .Header .Social-decor {
      display: none; }
  .App-wideScreenControls .Social {
    display: none;
    position: fixed;
    right: 0;
    top: 30px;
    z-index: 100; }
    @media screen and (min-width: 75rem) {
      .App-wideScreenControls .Social {
        display: flex; } }
